import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Layout, Tabs } from 'antd';
import AccountList from './containers/account'
import UserList from './containers/user'
import OrderList from './containers/orders'
import ExceptionAccountList from './containers/exceptionAccounts'
import Setting from './containers/settings'
import FinanceList from './containers/finance'
import DictManage from './containers/dict'

const { TabPane } = Tabs
const { Header, Content } = Layout;
class App extends Component {
    constructor(props) {
        super(props)
    }

    handleChange(value) {
        if (value == 0) {
            this.account.fecthAccounts()
        } else if (value == 1 && this.user) {
            this.user.fetchUsers(1)
        } else if (value == 2 && this.order) {
            this.order.fetchOrders()

        } else if (value == 3 && this.outtime) {
            this.outtime.fecthAccounts()

        } else if (value == 4 && this.finance) {
            this.finance.fetchList(1)

        }

    }
    render() {
        return (
            <div className="App" >
                <Layout style={{ height: '100%' }}>
                    <Header style={{ color: '#fff', fontSize: '24px', textAlign: 'left' }}>
                        包猪公微信机器人管理后台
                </Header>
                    <Layout>
                        <Content className="page-content">
                            <Tabs onChange={this.handleChange.bind(this)}>
                                <TabPane tab="账号列表" key={0}>
                                    <AccountList ref={ref => this.account = ref} />
                                </TabPane>
                                <TabPane tab="用户列表" key={1}>
                                    <UserList ref={ref => this.user = ref} />
                                </TabPane>
                                <TabPane tab="订单列表" key={2}>
                                    <OrderList ref={ref => this.order = ref} />
                                </TabPane>
                                <TabPane tab="超时账号" key={3}>
                                    <ExceptionAccountList ref={ref => this.outtime = ref} />
                                </TabPane>
                                <TabPane tab="财务管理" key={4}>
                                    <FinanceList ref={ref => this.finance = ref} />
                                </TabPane>
                                <TabPane tab="系统设置" key={5}>
                                    <Setting />
                                </TabPane>
                                <TabPane tab="关键词设置" key={6}>
                                    <DictManage />
                                </TabPane>
                            </Tabs>
                        </Content>
                    </Layout>

                </Layout>
            </div>
        );
    }

}

export default App;
