import React from 'react'
import { Table, Pagination, Button } from 'antd'
import APIService from '../service/APIService'
import { formdateTime } from '../utils/utils'

class DictList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            dataSource: [],
            curPage: 0,
            pageSize: 10,
            total: 0
        }
    }

    componentDidMount() {
        console.log('this.props.type=====')
        this.fetchList(1, this.props.type)
    }

    fetchList(page, type) {
        this.setState({
            curPage: page ? page : 0
        }, () => {
            let { curPage, pageSize } = this.state
            let params = {
                lab: type,
                page: page ? page - 1 : curPage,
                size: pageSize
            }
            APIService.WeChatRobotAPI('configList', params).then(res => {
                this.setState({
                    dataSource: res.data.content,
                    total: res.data.totalElements
                })
            })
        })

    }


    handleChangePage(page) {
        this.fetchList(page, this.props.type)
    }

    handleEdit(record) {
        this.props.handleEdit(record, this.props.type)
    }
    handleDel(record) {
        this.props.handleDel(record, this.props.type)
    }

    render() {
        const { dataSource, curPage, pageSize, total } = this.state

        const columns = [
            {
                title: 'ID',
                dataIndex: 'id',
                key: 'id',
            },
            {
                title: '名称',
                dataIndex: 'lab',
                key: 'lab',
            },
            {
                title: '值',
                dataIndex: 'value',
                key: 'value',
            },
            {
                title: '操作', dataIndex: 'operation', width: 160, fixed: 'right', render: (text, record, index) => {
                    return (
                        <div style={{ display: 'flex' }}>
                            <Button type='link' onClick={this.handleEdit.bind(this, record)}>编辑</Button>
                            <Button type='link' onClick={this.handleDel.bind(this, record)}>删除</Button>
                        </div>
                    )
                }
            }
        ]

        return (
            <div>
                <Table
                    style={{ marginBottom: 20 }}
                    columns={columns}
                    dataSource={dataSource}
                    pagination={false}
                />
                <Pagination defaultCurrent={curPage} total={total} pageSize={pageSize} onChange={this.handleChangePage.bind(this)} showSizeChanger={false} />

            </div>
        )
    }
}

export default DictList