import React from 'react'
import { Table, Pagination, Row, Col, DatePicker, Button } from 'antd'
import APIService from '../service/APIService'
import { formdateTime } from '../utils/utils'
import moment from 'moment';

const { RangePicker } = DatePicker;

const dateFormat = 'YYYY-MM-DD';
class FinanceList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            dataSource: [],
            curPage: 0,
            pageSize: 10,
            total: 0,
            start: moment().endOf('day').subtract(2, 'days'),
            end: moment().endOf('day').subtract(1, 'days')
        }
    }

    componentDidMount() {
        this.fetchList()
        this.fetchTotal()
    }

    fetchList(page) {
        this.setState({
            curPage: page ? page : 0
        }, () => {
            let { curPage, pageSize } = this.state
            let params = {
                page: page ? page - 1 : curPage,
                size: pageSize,
                start: '2020-09-01',
                end: '2020-12-01'
            }
            APIService.WeChatRobotAPI('financeList', params).then(res => {
                this.setState({
                    dataSource: res.data,
                })
            })
        })

    }


    fetchTotal() {
        APIService.WeChatRobotAPI('financeTotal', {}).then(res => {
            console.log('res====', res)
            if (res.code == 200) {
                this.setState({
                    totalNum: res.data
                })
            }
            // this.setState({
            //     dataSource: res.data.content,
            //     total: res.data.totalElements
            // })
        })
    }

    handleChangePage(page) {
        this.fetchOrders(page)
    }

    handleGetTimes(e) {
        console.log(e)

        if (e.length == 2) {
            // let startStr = moment(parseInt(e[0])).format('YYYY-MM-DD')
            let startStr = moment(new Date(e[0]._d)).format('YYYY-MM-DD')
            let endStr = moment(new Date(e[1]._d)).format('YYYY-MM-DD')

            // let _startTime = new Date(e[0])
            // let _startYear = new Date(e[0]).getFullYear()
            // let _startMonth = parseInt(_startTime.getMonth() + 1) > 9 ? parseInt(_startTime.getMonth() + 1) : '0' + parseInt(_startTime.getMonth() + 1)
            // let _startDay = _startTime.getDay() > 9 ? _startTime.getDay() : '0' + _startTime.getDay()
            // let startStr = _startYear + '-' + _startMonth + '-' + _startDay

            this.setState({
                start: startStr,
                end: endStr
            })

        }
    }

    handleQuery() {

    }
    render() {
        const { dataSource, curPage, pageSize, total, totalNum } = this.state

        const columns = [
            {
                title: '货架号',
                dataIndex: 'id',
                key: 'id',
            },
            {
                title: '账号名称',
                dataIndex: 'username',
                key: 'username',
            },
            {
                title: '游戏名',
                dataIndex: 'accountId',
                key: 'accountId',
                render: (text, record, index) => {
                    return (
                        <span>英雄联盟</span>
                    )
                }
            },
            {
                title: '大区',
                dataIndex: 'server',
                key: 'server',
            },
            {
                title: '角色名',
                dataIndex: 'nick_name',
                key: 'nick_name',
            },
            {
                title: '次数',
                dataIndex: 'count',
                key: 'count',
            },
            {
                title: '小时数',
                dataIndex: 'times',
                key: 'times',
            },
            {
                title: '盈利',
                dataIndex: 'amount',
                key: 'amount',
            },

        ]

        return (
            <div>
                <Row style={{ display: 'flex', justifyContent: 'space-between', marginBottom: 20 }}>

                    <Col span={12}>
                        <RangePicker
                            style={{ float: 'left', marginRight: 10 }}
                            defaultValue={[moment().startOf('day').subtract(2, 'days'), moment().endOf('day').subtract(1, 'days')]}
                            format={dateFormat}
                            onChange={this.handleGetTimes.bind(this)}
                        />
                        <Button type="primary" onClick={this.handleQuery.bind(this)}>查询</Button>
                    </Col>
                    <Col span={12} style={{ width: '300px', textAlign: 'right' }}>总盈利：{totalNum}元</Col>
                </Row>
                <Table
                    style={{ marginBottom: 20 }}
                    columns={columns}
                    dataSource={dataSource}
                />

            </div>
        )
    }
}

export default FinanceList