import React from 'react'
import { Table, Drawer, Button, Form, Input, Select, Col, Row, message, Modal, InputNumber } from 'antd'
import APIService from '../service/APIService'
const { Option } = Select
const rankLevelSingleOpts = [
    { key: 0, value: '黑铁' },
    { key: 1, value: '青铜' },
    { key: 2, value: '白银' },
    { key: 3, value: '黄金' },
    { key: 4, value: '铂金' },
    { key: 5, value: '钻石' },
    { key: 6, value: '大师' },
    { key: 7, value: '王者' },
    { key: -1, value: '无段位' },

]
const rankLevelFlexibleOpts = {}

const rankLevelNumOpts = [
    { key: 'Ⅰ', value: 'Ⅰ' },
    { key: 'Ⅱ', value: 'Ⅱ' },
    { key: 'Ⅲ', value: 'Ⅲ' },
    { key: 'Ⅳ', value: 'Ⅳ' },
    { key: 'Ⅴ', value: 'Ⅴ' },
]

const vipsLevelOpts = [
    { key: 0, value: '普通' },
    { key: 1, value: 'VIP' },
    { key: 2, value: 'SVIP' },
]

const statusOpts = [
    { key: 0, value: '正常' },
    { key: 1, value: '使用中' },
    { key: 2, value: '其他' },


]

const regionList = [
    '艾欧尼亚',
    '祖安',
    '诺克萨斯',
    '班德尔城',
    '皮尔特沃夫',
    '战争学院',
    '巨神峰',
    '雷瑟守备',
    '裁决之地',
    '黑色玫瑰',
    '暗影岛',
    '钢铁烈阳',
    '水晶之痕',
    '均衡教派',
    '影流',
    '守望之海',
    '征服之海',
    '卡拉曼达',
    '皮城警备',
    '比尔吉沃特',
    '德玛西亚',
    '弗雷尔卓德',
    '无畏先锋',
    '恕瑞玛',
    '扭曲丛林',
    '巨龙之巢',
    '教育网专区',
    '男爵领域'
]
class AccountList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            dataSource: [
                {}
            ],
            basicDataSource: [{}],
            initData: {
                rankLevelName: '0',
                rankLevelFlexibleNum: '0'
            },
            visible: false,
            visible: false,
            balanceNum: null,
            choosenAccount: null,
            queryValue: '',
            queryServer: null

        }
    }

    formRef = React.createRef()


    componentDidMount() {
        this.fecthAccounts()
    }

    fecthAccounts() {
        APIService.WeChatRobotAPI('fetchAccounts', { wxid: 'root' }).then(res => {
            console.log('res======', res)
            this.setState({
                basicDataSource: res.data,
                dataSource: res.data
            })
        })
    }

    handleEditAccount(record) {
        let rankLevelNum = record.rankLevelSingle ? record.rankLevelSingle.substring(2, record.rankLevelSingle.length) : null
        // 灵活排具体值
        let rankFlexLevelNum = record.rankLevelFlexible ? record.rankLevelFlexible.substring(2, record.rankLevelFlexible.length) : null

        let editData = {
            ...record,
            rankLevelNum: rankLevelNum,
            rankLevelName: record.rankIndexSingle ? record.rankIndexSingle : -1,
            rankLevelFlexibleName: record.rankIndexFlexible ? record.rankIndexFlexible : -1,
            rankLevelFlexibleNum: rankFlexLevelNum,
            chessIndex: record.chessIndex ? record.chessIndex : -1
        }

        console.log('record====', record)
        console.log('editData====', editData)

        this.setState({
            visible: true,
            saveType: 'edit',
            initData: editData
        }, () => {
            this.formRef.current.setFieldsValue(editData)

        })
        // 单双排具体值


        console.log('editData=====', editData)

        console.log('.current.setFieldsValue(editData)=====', this.formRef)


    }
    onFinish(values) {
        let params = {
            ...values,
            rankIndexSingle: values.rankLevelName ? values.rankLevelName : 0,
            rankLevelSingle: rankLevelSingleOpts.find(item => item.key === values.rankLevelName).value + values.rankLevelNum,
            rankIndexFlexible: values.rankLevelFlexibleName ? values.rankLevelFlexibleName : 0,
            rankLevelFlexible: rankLevelSingleOpts.find(item => item.key === values.rankLevelFlexibleName).value + values.rankLevelFlexibleNum,
            chessIndex: rankLevelSingleOpts.find(item => item.key === values.chessLevel) != null ? rankLevelSingleOpts.find(item => item.key === values.chessLevel).key : -1,
            chessLevel: rankLevelSingleOpts.find(item => item.key === values.chessLevel) ? rankLevelSingleOpts.find(item => item.key === values.chessLevel).value : -1

        }
        if (this.state.saveType === 'edit') {
            params.id = this.state.initData.id
        }
        delete values.rankLevelName
        delete values.rankLevelNum
        delete values.rankLevelFlexibleName
        delete values.rankLevelFlexibleNum

        console.log('params=====', params)
        APIService.WeChatRobotAPI('saveAccounts', params).then(res => {
            if (res.code === 200) {
                message.success('保存成功！')
                this.fecthAccounts()
                this.setState({
                    visible: false
                })
            }
        })
    }


    /**
    * 下号
    */
    handleLoginOut(record) {
        this.setState({
            calcelVisible: true,
            choosenAccount: record
        })
    }

    getBalanceNum(value) {
        this.setState({
            balanceNum: value
        })
    }



    handleOk() {
        let { balanceNum, choosenAccount } = this.state
        if (balanceNum) {
            let params = {
                accountIds: [choosenAccount.id],
                costTime: balanceNum
            }
            APIService.WeChatRobotAPI('cancelRent', params).then(res => {
                if (res.code == 200) {
                    message.success('下号成功')
                    this.setState({
                        choosenAccount: null,
                        calcelVisible: false,
                        balanceNum: null
                    }, () => {
                        this.fecthAccounts()
                    })
                } else {
                    message.warning(res.message)
                }

            })
        }

    }

    handleCancel() {
        this.setState({
            calcelVisible: false
        })
    }

    handleSetRentGroup(record) {
        let params = {
            userId: record.id,
            status: !record.isRentGroup ? 1 : 0
        }
        APIService.WeChatRobotAPI('rentGroupChange', params).then(res => {
            console.log(res)


            this.fecthAccounts()
        })
    }

    // 点击新增
    handleAdd() {
        let initData = {
            rankLevelNum: null,
            rankLevelName: -1,
            rankLevelFlexibleName: -1,
            rankLevelFlexibleNum: null
        }
        this.setState({

            visible: true,
            saveType: 'add'
        }, () => {
            this.formRef.current.setFieldsValue(initData)
        })
    }

    // 获取输入框中的值
    handleGetFilterValue(e) {
        this.setState({
            queryValue: e.target.value
        })
    }
    // 前端查询过滤
    handleFilter() {
        let { queryValue, queryServer, basicDataSource } = this.state
        let list = []
        if (queryValue != '') {
            list = basicDataSource.filter((item => item.id == queryValue || item.username.indexOf(queryValue) > -1))
        } else {
            list = basicDataSource
        }
        if (queryServer) {
            console.log(queryServer)
            list = list.filter(item => item.server == queryServer)
        }
        this.setState({
            dataSource: list
        })
    }

    handleAllCancel() {
        Modal.confirm({
            title: '提示！',
            content: '是否确认进行全部下号操作？',
            okText: '确认',
            cancelText: '取消',
            onOk() {
                let that = this
                let params = {
                    isAllCancel: true
                }
                APIService.WeChatRobotAPI('cancelRent', params).then(res => {
                    if (res.code == 200) {
                        message.success('下号成功')
                        that.setState({
                            choosenAccount: null,
                            calcelVisible: false,
                            balanceNum: null
                        }, () => {
                            that.fecthAccounts()
                        })
                    } else {
                        message.warning(res.message)
                    }

                })
            },
            onCancel() {
                console.log('Cancel');
            },

        })

    }

    /**
     * 选择大区
     */
    handleSelectServer(e) {

        this.setState({
            queryServer: e
        })
    }

    render() {
        const limitNumber = value => {
            if (typeof value === 'string') {
                return !isNaN(Number(value)) ? value.replace(/^(0+)|[^\d]/g, '') : ''
            } else if (typeof value === 'number') {
                return !isNaN(value) ? String(value).replace(/^(0+)|[^\d]/g, '') : ''
            } else {
                return ''
            }
        }
        const { dataSource, initData, visible, saveType, queryServer } = this.state
        const columns = [
            { title: '账号', dataIndex: 'username', key: 'username', width: 200 },
            { title: '密码', dataIndex: 'password', key: 'password', width: 200 },
            { title: '昵称', dataIndex: 'nickName', key: 'nickName', width: 260 },
            { title: '货架号', dataIndex: 'id', key: 'id', width: 160 },
            { title: '大区', dataIndex: 'server', key: 'server', width: 160 },
            {
                title: 'VIP限制等级', dataIndex: 'vipLevel', width: 160, key: 'vipLevel', render: (text, record, index) => {
                    return (
                        <div>
                            {
                                text == 0 ?
                                    <span style={{ color: '#008000' }}>普通</span>
                                    :
                                    text == 1 ?
                                        <span style={{ color: '#FF8C00' }}>VIP</span>
                                        :
                                        <span style={{ color: '#ff0000' }}>SVIP</span>

                            }

                        </div>
                    )
                }
            },
            {
                title: '状态', dataIndex: 'status', key: 'status', width: 200, render: (text, record, index) => {
                    return (
                        <div>
                            {
                                text == 0 ?
                                    <span style={{ color: '#008000' }}>正常</span>
                                    :
                                    text == 1 ?
                                        <span style={{ color: '#FF8C00' }}>使用中</span>
                                        :
                                        <span style={{ color: '#ff0000' }}>不可用</span>

                            }

                        </div>
                    )
                }
            },
            { title: '单双排段位', dataIndex: 'rankLevelSingle', key: 'rankLevelSingle', width: 160, },
            { title: '灵活排位段位', dataIndex: 'rankLevelFlexible', key: 'rankLevelFlexible', width: 160, },
            { title: '云顶段位', dataIndex: 'chessLevel', key: 'chessLevel', width: 160, },
            { title: '英雄数', dataIndex: 'heroNum', key: 'heroNum', width: 100 },
            { title: '等级', dataIndex: 'level', key: 'level', width: 100 },
            { title: '租号单价（元/时）', dataIndex: 'price', key: 'price', width: 160, },
            { title: '英雄', dataIndex: 'heroList', key: 'heroList', ellipsis: true, width: 300 },
            {
                title: '操作', dataIndex: 'operation', width: 160, fixed: 'right', render: (text, record, index) => {
                    return (
                        <div style={{ display: 'flex' }}>
                            <Button type='link' onClick={this.handleEditAccount.bind(this, record)}>编辑</Button>
                            <Button type='link' onClick={this.handleLoginOut.bind(this, record)}>下号</Button>
                        </div>
                    )
                }
            }

        ]


        return (
            <div>
                <Row style={{ marginBottom: 20, display: 'flex' }}>
                    <Select style={{ marginRight: 10, width: 100 }} allowClear onChange={this.handleSelectServer.bind(this)}>
                        {
                            regionList.map((item, index) => {
                                return (
                                    <Option value={item} key={item}>{item}</Option>
                                )
                            })
                        }
                    </Select>
                    <Input style={{ width: 200, marginRight: 8 }} allowClear placeholder="请输入货架号" onChange={this.handleGetFilterValue.bind(this)}></Input>
                    <Button style={{ marginRight: 8 }} onClick={this.handleFilter.bind(this)} type="success" >查询</Button>
                    <Button onClick={this.handleAdd.bind(this)} type="primary">新增</Button>
                    <Button onClick={this.handleAllCancel.bind(this)} style={{ marginLeft: 20 }} >全部下号</Button>
                </Row>

                <Table
                    columns={columns}
                    dataSource={dataSource}
                    scroll={{ x: 1800, y: 'auto' }}
                />
                <Drawer
                    visible={visible}
                    width={600}
                    title={saveType == 'edit' ? '编辑' : '新建'}
                    onClose={() => { this.setState({ visible: false }) }}

                >
                    <Form
                        ref={this.formRef}
                        labelCol={{ span: 6 }}
                        wrapperCol={{ span: 14 }}
                        layout="horizontal"
                        onFinish={this.onFinish.bind(this)}
                    >
                        <Form.Item label="账号状态" name="status" required={true}>
                            <Select>
                                {
                                    statusOpts.map((item, index) => {
                                        return (
                                            <Select.Option value={item.key} key={item.key}>{item.value}</Select.Option>
                                        )
                                    })
                                }
                            </Select>
                        </Form.Item>

                        <Form.Item label="账号" name="username" required={true}>
                            <Input placeholder="请输入账号" />
                        </Form.Item>
                        <Form.Item label="密码" name="password" required={true}>
                            <Input.Password placeholder="请输入密码" />
                        </Form.Item>
                        <Form.Item label="昵称" name="nickName">
                            <Input placeholder="请输入昵称" />
                        </Form.Item>
                        <Form.Item label="所在大区" name="server" required={true}>
                            <Select>
                                {
                                    regionList.map((item, index) => {
                                        return (
                                            <Select.Option value={item} key={item}>{item}</Select.Option>
                                        )
                                    })
                                }
                            </Select>
                        </Form.Item>
                        <Form.Item label="VIP限制等级" name="vipLevel">
                            <Select>
                                {
                                    vipsLevelOpts.map((item, index) => {
                                        return (
                                            <Select.Option value={item.key} key={item.key}>{item.value}</Select.Option>
                                        )
                                    })
                                }
                            </Select>
                        </Form.Item>
                        <Form.Item label="单双排段位">
                            <Form.Item name="rankLevelName">

                                <Select placeholder="请选择单双排段位">
                                    {
                                        rankLevelSingleOpts.map((item, index) => {
                                            return (
                                                <Select.Option key={item.key} value={item.key}>{item.value}</Select.Option>
                                            )
                                        })
                                    }
                                </Select>
                            </Form.Item>

                            <Form.Item name="rankLevelNum">
                                <Select>
                                    {
                                        rankLevelNumOpts.map((item, index) => {
                                            return (
                                                <Select.Option key={item.key} value={item.key}>{item.value}</Select.Option>
                                            )
                                        })
                                    }
                                </Select>
                            </Form.Item>

                        </Form.Item>
                        <Form.Item label="灵活排位段位">
                            <Form.Item name="rankLevelFlexibleName">
                                <Select placeholder="请选择单双排段位">
                                    {
                                        rankLevelSingleOpts.map((item, index) => {
                                            return (
                                                <Select.Option key={item.key} value={item.key}>{item.value}</Select.Option>
                                            )
                                        })
                                    }
                                </Select>
                            </Form.Item>
                            <Form.Item name="rankLevelFlexibleNum">
                                <Select>

                                    {
                                        rankLevelNumOpts.map((item, index) => {
                                            return (
                                                <Select.Option key={item.key} value={item.key}>{item.value}</Select.Option>
                                            )
                                        })
                                    }

                                </Select>
                            </Form.Item>
                        </Form.Item>
                        <Form.Item label="云顶段位">
                            <Form.Item name="chessLevel">
                                <Select placeholder="请选择单双排段位">
                                    {
                                        rankLevelSingleOpts.map((item, index) => {
                                            return (
                                                <Select.Option key={item.key} value={item.key}>{item.value}</Select.Option>
                                            )
                                        })
                                    }
                                </Select>
                            </Form.Item>
                        </Form.Item>

                        <Form.Item label="英雄数量" name="heroNum">
                            <Input placeholder="请输入昵称" />
                        </Form.Item>
                        <Form.Item label="等级" name="level">
                            <Input placeholder="请输入昵称" />
                        </Form.Item>
                        <Form.Item label="租号单价（元/时）" name="price">
                            <Input placeholder="请输入租号单价" />
                        </Form.Item>
                        <Form.Item label="英雄" name="heroList">
                            <Input.TextArea placeholder="请输入英雄" />
                        </Form.Item>
                        <Form.Item >
                            <Button type="primary" htmlType="submit">提交</Button>
                        </Form.Item>
                    </Form>
                </Drawer>

                <Modal
                    title="下号"
                    visible={this.state.calcelVisible}
                    onOk={this.handleOk.bind(this)}
                    onCancel={this.handleCancel.bind(this)}
                    okText="确认"
                    cancelText="取消"
                >
                    <div style={{ display: 'flex', flexDirection: 'row' }}>
                        <div style={{ flex: 2 }}>时长（分钟）</div>
                        <InputNumber step={1} style={{ flex: 3 }} onChange={this.getBalanceNum.bind(this)} parser={limitNumber}></InputNumber>
                    </div>
                </Modal>
            </div>
        )
    }
}

export default AccountList