import React, { Component } from 'react'
import { Layout, Tabs, Button, Drawer, Form, Select, Input, message } from 'antd';
import DictList from '../component/dictList'
import APIService from '../service/APIService';

const { TabPane } = Tabs
const { Header, Content } = Layout;
const labArry = [
    { key: 1, value: '回收' },
    { key: 2, value: '全部回收' },
    { key: 3, value: '查询语句' },
    { key: 4, value: '黑名单' },
]

class DictManage extends Component {
    constructor(props) {
        super(props)
        this.state = {
            curTab: 1,
            saveType: 'add',
            visible: false,
            initData: null
        }
    }

    // 新增
    handleAdd() {
        this.setState({
            saveType: 'add',
            visible: true,
            initData: null
        })
    }

    // 编辑
    editDict(obj, type) {
        console.log('obj=====', obj)
        this.setState({
            initData: obj,
            saveType: 'edit',
            visible: true,
        })
    }

    delDict(obj) {
        let params = {
            id: obj.id
        }
        console.log('params====', params)
        APIService.WeChatRobotAPI('configDel', params).then(res => {
            if (res.code == 200) {
                message.success('删除成功！')
                this.handleChangeTab(obj.key)
            }
        })

    }

    onFinish(values) {
        const { saveType, initData } = this.state
        let lebStr = ''
        if (values.key == 1) {
            lebStr = '回收'
        } else if (values.key == 2) {
            lebStr = '全部回收'

        } else if (values.key == 3) {
            lebStr = '查询语句'

        } else if (values.key == 4) {
            lebStr = '黑名单'
        }

        let params = {
            ...values,
            lab: lebStr
        }

        if (saveType == 'edit') {
            params.id = initData.id
        }
        APIService.WeChatRobotAPI('configSave', params).then(res => {
            if (res.code == 200) {
                message.success('保存成功！')
                this.setState({
                    visible: false,
                    initData: null
                }, () => {
                    this.handleChangeTab(params.key)
                })
            }
        })
    }


    handleChangeTab(value) {
        this.setState({
            curTab: value
        }, () => {
            console.log('value====', value)
            // if (value == 1) {
            //     this.listOne.fetchList(1, '回收')
            // } else if (value == 2) {
            //     this.listTwo.fetchList(1, '全部回收')

            // } else if (value == 3) {
            //     this.listThree.fetchList(1, '查询语句')

            // } else if (value == 4) {
            //     this.listFour.fetchList(1, '黑名单')
            // }

        })
    }
    render() {
        const { visible, saveType, initData, curTab } = this.state
        console.log('curTab=======', curTab)
        return (
            <Layout>
                <Content className="page-content" style={{ position: 'relative' }}>
                    <Button type="primary" onClick={this.handleAdd.bind(this)} style={{ position: 'absolute', right: 50, top: 25, zIndex: 99 }}>新增</Button>
                    <Tabs onChange={this.handleChangeTab.bind(this)} activeKey={curTab.toString()} >
                        <TabPane tab="回收" key={1}>
                            <DictList type="回收" handleEdit={this.editDict.bind(this)} handleDel={this.delDict.bind(this)} ref={ref => this.listOne = ref} />
                        </TabPane>
                        <TabPane tab="全部回收" key={2}>
                            <DictList type="全部回收" handleEdit={this.editDict.bind(this)} handleDel={this.delDict.bind(this)} ref={ref => this.listTwo = ref} />
                        </TabPane>
                        <TabPane tab="查询语句" key={3}>
                            <DictList type="查询语句" handleEdit={this.editDict.bind(this)} handleDel={this.delDict.bind(this)} ref={ref => this.listThree = ref} />
                        </TabPane>
                        <TabPane tab="黑名单" key={4}>
                            <DictList type="黑名单" handleEdit={this.editDict.bind(this)} handleDel={this.delDict.bind(this)} ref={ref => this.listFour = ref} />
                        </TabPane>
                    </Tabs>
                </Content>
                <Drawer
                    visible={visible}
                    width={600}
                    title={saveType == 'edit' ? '编辑' : '新建'}
                    onClose={() => { this.setState({ visible: false }) }}

                >
                    <Form
                        labelCol={{ span: 6 }}
                        wrapperCol={{ span: 14 }}
                        layout="horizontal"
                        initialValues={initData}
                        onFinish={this.onFinish.bind(this)}
                    >
                        <Form.Item label="名称" name="key" required={true}>
                            <Select>
                                {
                                    labArry.map((item, index) => {
                                        return (
                                            <Select.Option value={item.key} key={item.key}>{item.value}</Select.Option>
                                        )
                                    })
                                }
                            </Select>
                        </Form.Item>
                        <Form.Item label="值" name="value" required={true}>
                            <Input placeholder="请输入值" />
                        </Form.Item>
                        <Form.Item >
                            <Button type="primary" htmlType="submit">提交</Button>
                        </Form.Item>
                    </Form>
                </Drawer>
            </Layout>
        )

    }
}
export default DictManage