import React from 'react'
import { Table, Pagination, Col, Switch, Row, message } from 'antd'
import APIService from '../service/APIService'
import { formdateTime } from '../utils/utils'

class Setting extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            openStatus: null, // 1-开启， 0关闭
            checkStatus: '1',
            cardStatus: '1'
        }
    }

    componentDidMount() {
        this.fetchMode()
        this.fetchCard()
    }

    fetchMode() {
        APIService.WeChatRobotAPI('fetchMode', {}).then(res => {
            if (res.code === 200) {
                this.setState({
                    checkStatus: res.data
                })
            }
        })
    }
    fetchCard() {
        APIService.WeChatRobotAPI('cardGet', {}).then(res => {
            if (res.code === 200) {
                this.setState({
                    cardStatus: res.data
                })
            }
        })
    }

    handleChangeModeStatus(checked) {
        console.log(checked)
        let params = {
            status: checked ? '1' : '0'
        }
        APIService.WeChatRobotAPI('saveMode', params).then(res => {
            if (res.code === 200) {
                message.success('操作成功')
                this.fetchMode()

            }
        })
    }
     handleChangeCardStatus(checked) {
        let params = {
            status: checked ? '1' : '0'
        }
        APIService.WeChatRobotAPI('cardSave', params).then(res => {
            if (res.code === 200) {
                message.success('操作成功')
                this.fetchCard()

            }
        })
    }
    render() {
        const { checkStatus, cardStatus } = this.state
        return (
            <div>
                <Row>
                    <Col span={2}>静默模式</Col>
                    <Col span={4}>
                        <Switch checked={checkStatus === '1' ? true : false} checkedChildren="开启" unCheckedChildren="关闭" style={{ float: 'left' }} onChange={this.handleChangeModeStatus.bind(this)} />
                    </Col>
                </Row>
                <Row style={{marginTop: 20}}>
                    <Col span={2}>名片模式</Col>
                    <Col span={4}>
                        <Switch checked={cardStatus === '1' ? true : false} checkedChildren="开启" unCheckedChildren="关闭" style={{ float: 'left' }} onChange={this.handleChangeCardStatus.bind(this)} />
                    </Col>
                </Row>
            </div>
        )
    }
}

export default Setting